import { Component, Input, Output, EventEmitter, OnInit } from "@angular/core";
import { FormGroup, FormControl, Validators } from "@angular/forms";
import { Observable } from "rxjs/Observable";

import { ResetPasswordRequestModel } from "../../models/reset-password-request.model";
import { MatSnackBar } from "@angular/material";
import { BehaviorSubject } from "rxjs/BehaviorSubject";
import { map, takeWhile } from "rxjs/operators";

@Component({
	selector: "user-reset-password-request",
	template: `<form [formGroup]="formGroup" (ngSubmit)="submit()" fxLayout="column">
  <mat-card>
    <mat-card-header>
      <mat-card-title>
        <h3>   </h3>
      </mat-card-title>
    </mat-card-header>
    <mat-card-content>
      <p>         </p>
      <mat-radio-group fxFlexFill formControlName="Type">
        <mat-radio-button value="sms"></mat-radio-button>
        <mat-radio-button value="email"> </mat-radio-button>
      </mat-radio-group>
      <mat-form-field fxFlexFill>
        <input matInput placeholder="  " formControlName="Username">
      </mat-form-field>
    </mat-card-content>
    <mat-card-actions>
      <!--<button type="submit" [disabled]='(canRequestPin | async) || (maximumResendingHappend$ | async)' (click)="submit()" mat-raised-button color="primary">-->
      <button type="submit" [disabled]='(canRequestPin | async)' (click)="submit()" mat-raised-button color="primary">
        <span *ngIf='!(canRequestPin | async)'>
            
        </span>
        <!--<div *ngIf='(canRequestPin | async) && !(maximumResendingHappend$ | async)'>
          {{timer$ | async}}        .
        </div>
        <div *ngIf='(maximumResendingHappend$ | async)'>
            
        </div>-->
      </button>
      <a mat-button routerLink='/auth/signin'></a>
    </mat-card-actions>
  </mat-card>
</form>
`,
	styles: [``]
})
export class ResetPasswordRequestComponent implements OnInit {
	@Output() submitted = new EventEmitter<ResetPasswordRequestModel.Request>();

	@Input() formGroup: FormGroup;
	@Input() numberOfRequested: Observable<number>;
	@Input() canRequestPin: Observable<boolean>;
	@Input()
	set pending(isPending: boolean) {
		if (isPending) {
			this.formGroup.disable();
		}

		this.formGroup.enable();
	}

	timer$: Observable<number>;
	maximumResendingHappend$ = new BehaviorSubject(false);

	constructor(public snackBar: MatSnackBar) {
		this.timer$ = Observable.timer(0, 100).pipe(
			map(i => i + 1),
			takeWhile(i => i * 100 <= 3000),
			map(i => 3000 - i * 100)
		);
	}

	ngOnInit() {
		this.numberOfRequested.subscribe(data => {
			if (data > 2) {
				this.maximumResendingHappend$.next(true);
			}
		});
	}

	submit() {
		if (this.formGroup.valid) {
			this.submitted.emit(this.formGroup.value);
			let message = this.maximumResendingHappend$.getValue()
				? "  ..."
				: "     .";
			this.snackBar.open(message, "", {
				duration: 3000
			});
		}
	}
}
